import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-export-kitchen',
  templateUrl: './export-kitchen.component.html',
  styleUrls: ['./export-kitchen.component.scss']
})
export class ExportKitchenComponent implements OnInit {

  public searchValue: string = ''
  public invoiceShow: any[] = []
  public invoiceToday: any[] = [];
  tableHeight: string;
  startTime: Date = new Date()
  endTime: Date = new Date()
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  public filterType: 1 | 2 = 1;
   
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private cdRef: ChangeDetectorRef
  ) {
    this.startTime.setHours(0, 0, 0, 0);
    this.endTime.setHours(23, 59, 59, 59);
  }
  listFilter: Array<any> = [{ value: 1, name: "Latest" }, { value: 2, name: "Oldest" }]
  ngOnInit() {
    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue
      this.filterType = this.dataRestore.filterType
      
    }
    else { this.filterType = 1;  }
    if (!this.invoiceToday) this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => this.getInvoiceToday(this.filterType))
    else this.getInvoiceToday(this.filterType)
  } 
  /**
   * get danh sách hóa đơn xuất cho bếp hôm nay (billtype 3)
   * @example this.getInvoiceToday()
   */
  getInvoiceToday(filterType?) {
    this.vhQueryCafe.getBills_byFields( { date: { $gte: this.startTime, $lte: this.endTime }, bill_type: { $in: [3, 15, 23] }, id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id } }, {})
      .then((invoice: any[]) => {
        console.log(invoice);
        
        if (!this.invoiceToday) this.vhComponent.hideLoading(0)
        this.invoiceToday = invoice.map(item => {
          return { ...item, employee_name: this.renderEmployee(item.id_employee), status: this.languageService.translate('Issued') }
        })
        if (filterType) this.sortFilter(filterType)
        else this.searchBill(this.searchValue);
      })
  }
  /**
   * trả về tên nv tạo đơn
   * @example let employee = this.renderEmployee('DNIWUHDNIKWUHDQOIUH')
   */
  renderEmployee(id_employee: string) {
    let employee: string = this.vhAuth.getlocalEmployee(id_employee).name

    return employee ? employee : this.languageService.translate("Not found")
  }
  /**
   * lọc hóa đơn theo ['bill_code', 'partner_name', 'payment_name', 'name_employee', 'status']
   * @param value 
   * @example this.searchBill('')
   */
  searchBill(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoiceToday, ['bill_code', 'partner_name', 'employee_name', 'payment_name', 'status', 'phone'])
    } else this.invoiceShow = [...this.invoiceToday]
  } 
  /**
  * sort invoiceToday 
  * @param filterType 
  * @example  this.sortFilter(1)
  */
  sortFilter(filterType: 1 | 2) {
    switch (filterType) {
      case 1: this.invoiceToday.sort((prev, next) => Date.parse(next.date) - Date.parse(prev.date));  break;
      case 2: this.invoiceToday.sort((prev, next) => Date.parse(prev.date) - Date.parse(next.date));  break;
    }

    this.searchBill(this.searchValue)
  }
  /* -----Pagination----- */
  onCurrentPageDataChange(value) {
  }
  ngAfterViewChecked() {
    if (document.querySelector("#sales-invoice-today") && document.querySelector(".ant-table-thead") && document.querySelector(".sales-invoice-today-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#sales-invoice-today").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".sales-invoice-today-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 80 + "px";
    }
    this.cdRef.detectChanges();
  }
  gotoAdd() {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType }
    if (this.vhAuth.checkMyPermission('issuing_enable')) {
      this.router.navigate(["/cafe/dashboard/management/invoice/export-kitchen/add"], { state: { dataRestore } })
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  goToDetail(id) {
    if (this.vhAuth.checkMyPermission('issuing_enable')) {
      let dataRestore = { searchValue: this.searchValue, filterType: this.filterType }
      this.router.navigate(["/cafe/dashboard/management/invoice/export-kitchen/detail"], { state: { id, dataRestore } })
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  goBack() {
    this.router.navigate(["/cafe/dashboard/management/invoice"])
  }


  /** hàm sort cho các cột */
  dateCol = false;
  bill_codeCol = false;
  employee_nameCol = false;
  statusCol = false;
  /** Hàm thực hiện sắp xếp theo collect
   *
   * @param colName       // tên cột muôn sắp xếp
   */
  sortTable(colName){
    switch (colName) {
      case 'date':
        if(this.dateCol){
          this.invoiceShow = this.vhAlgorithm.sortDatebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortDatebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'bill_code':
        if(this.bill_codeCol){
          this.invoiceShow = this.vhAlgorithm.sortStringbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortStringbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'employee_name':
        if(this.employee_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortStringbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortStringbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'status':
        if(this.statusCol){
          this.invoiceShow = this.vhAlgorithm.sortStringbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortStringbyDESC([...this.invoiceShow], colName);
        }
        break;
    }
  }
}
