import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { ExportKitchenComponent } from './export-kitchen.component';
import { AddComponent } from './add/add.component';
import { DetailComponent } from './detail/detail.component';
import { EditComponent } from './edit/edit.component';
import { BillType3Module } from 'src/app/cafe/components/bill-type/bill-type-3/bill-type3.module';
const routes: Routes = [
  {
    path: "",
    component: ExportKitchenComponent
  },
  {
    path: 'add',
    component: AddComponent
  },
  {
    path: 'detail',
    component: DetailComponent
  },
  {
    path: 'edit',
    component: EditComponent
  }
]

@NgModule({
  declarations: [
    ExportKitchenComponent,AddComponent
    ,DetailComponent,EditComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    ReactiveFormsModule,
    FormsModule,
    TranslateModule,
    BillType3Module
  ]
})
export class ExportKitchenModule { }
